<script>
import { Router, Route, navigate } from "svelte-routing";
import PXL from './utils/PXL.js';
import Header from './components/Header.svelte';
import Index from './Index.svelte';
import View from './View.svelte';
import Setting from './components/Setting.svelte';
import ErrorMessage from './components/ErrorMessage.svelte';

let apiBase = localStorage.getItem('apiBase') || 'https://api.pxl.ai/v1';
let apikey = localStorage.getItem('apikey') || '';
let demoImages = localStorage.getItem('demo-images') || '';
const pxl = new PXL(apiBase, apikey);
$: pxl.set(apiBase, apikey);

if (window.location.href.indexOf('?') > -1 && window.location.href.indexOf('base=') > -1 && window.location.href.indexOf('key=') > -1) {
  const params = window.location.href.split('?')[1].split('&');
  apiBase = params.filter(q => q.indexOf('base') === 0)[0].split('=')[1];
  apikey = params.filter(q => q.indexOf('key') === 0)[0].split('=')[1];
  localStorage.setItem('apiBase', apiBase)
  localStorage.setItem('apikey', apikey)
  navigate(window.location.href.split('?')[0]);
}
if (window.location.href.indexOf('?') > -1 && window.location.href.indexOf('redirect=') > -1) {
  const params = window.location.href.split('?')[1].split('&');
  let redirect = params.filter(q => q.indexOf('redirect') === 0)[0].split('=')[1];
  navigate(decodeURIComponent(redirect));
}
</script>

<Router>
  <Header />
  <Route path="/url/:url" component={View} pxl={pxl}></Route>
  <Route path="/"><Index pxl={pxl} demoImages={demoImages} /></Route>
  <Setting bind:apiBase={apiBase} bind:apikey={apikey} bind:demoImages={demoImages} />
  <ErrorMessage />
</Router>
